.conteudo-principal__populares {
  width: 80vw;
  height: auto;
  background: var(--preto-bg);
  padding: 30px 10vw;
}

.conteudo-principal__populares-container {
  height: 200px;
  background: var(--branco);
  margin: 20px 0;
  transition: all .8s;
  display: flex;
}

.conteudo-principal__populares-container:hover {
  cursor: pointer;
  box-shadow: 0px 0px 5px 2px var(--branco);
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

.conteudo-principal__populares-container-imagem {
  max-height: 200px;
  width: 220px;
  object-fit: cover;
  margin-right: 20px;
}

.conteudo-principal__populares-container__caixa-texto {
  padding: 20px;
}

.conteudo-principal__populares-container__caixa-texto-titulo {
  font-family: var(--fonte-padrao);
  text-transform: uppercase;
  font-size: 3rem;
  font-weight: 100;
}

@media screen and (max-width: 768px) {
  .conteudo-principal__populares-container {
    height: auto;
    flex-wrap: wrap;
  }

  .conteudo-principal__populares-container-imagem {
    max-width: none;
    width: 100%;
    margin: 0;
  }
}

